import React, { Component } from 'react';
import Modal from "./modal";

class AddBookButton extends Component {

  constructor(props) {
    super(props);
    this.state = { showModal: false }
  }

  handleShowModal = () => {
    this.setState({ showModal: true });
  };

  hideModal = () => {
    this.setState({ showModal: false });
  };

  render() { 
    return (
      <div className="add_book_wrapper">
        <Modal showModal={this.state.showModal} handleClose={this.hideModal.bind(this)}>
            <form action="">
              <label>Title*</label>
              <input type="text" name="" id="" placeholder="Enter Title"/>
              <label>Author*</label>
              <input type="text" name="" id="" placeholder="Enter Author"/>
              <label>Publisher*</label>
              <input type="text" name="" id="" placeholder="Enter Publisher"/>
              <label>Paperback</label>
              <input type="text" name="" id="" placeholder="Enter Paperback"/>
              <label>ISBN*</label>
              <input type="text" name="" id="" placeholder="Enter ISBN"/>
              <label>Summary</label>
              <input type="text" name="" id="" placeholder="Enter Summary"/>
            </form>
        </Modal>

        <button className="add_book_btn" onClick={this.handleShowModal}>ADD A BOOK</button>
      </div>
     );
  }
}



